import React from 'react';
import './index.css';

import useFullscreenStatus from "./useFullscreenStatus";
import App from './App';

export default function MaxView() {
    const maxView = React.useRef(null);
    let isFullscreen, setIsFullscreen;
    
    try {
      [isFullscreen, setIsFullscreen] = useFullscreenStatus(maxView);
    } catch (e) {
      isFullscreen = false;
      setIsFullscreen = undefined;
      console.log("error");
    }
  
    return(<App ref={ maxView } />);
  }